import { AfterViewInit, Component, Input, OnInit, ViewChild, } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { Observable } from 'rxjs';
@Component({
  selector: 'app-datatable',
  templateUrl: './datatable.component.html',
  styleUrls: ['./datatable.component.scss']
})
export class DatatableComponent implements OnInit, AfterViewInit {
  @ViewChild(MatPaginator) paginator!: MatPaginator; //Pour accder aux instances de la
  @ViewChild(MatSort) sort!: MatSort;


  @Input() data: any[];
  dataSource = new MatTableDataSource<any>;

  /*
  *Stock les noms des colonnes (les cls du tableau json reu )
  Ex:
  {
    "id":1
  }
  il stockera id
  */
  @Input() columns: string[]; //Les cl du json reu
  @Input() displayedColumns: string[]; //Les noms des colonnes  afficher dans le template

  @Input() labelDataTable: string; // Texte annoncateur. Ex: Liste des offres

  /*
  * Liste des actions  effectuer
  * On reoit le label et la mthode  excuter
  * On aura donc un sous menu du dropdown comportant plusieurs 
  * lien qui excute une mthode
  * Prcision: Dans les pages qui feront appel  ce component, 
  * il faut toujours norm le champs qui reprsente les actions actions
  * Car une conditions est fait sur a dans le template
  * action: (params: any) => void signifie qu'on recevra une fonction en paramtre de type void
  */
  @Input() actions: { label: string, action: (params: any) => void }[];

  /*
  * Liste des boutons  excuter 
  * On reoit le label, la couleur du bouton et la mthode  excuter
  */
  @Input() buttonsAction: { label: string, color: string, action: (params: any) => void }[];

  /*
  * Liste des bages ou status:
  * On reoit le code le label, et la couleur
  * 
  */
  @Input() badges: { code: number, label: string, color: string }[] = [];
  //On doit prciser le nom de la colonne que porte le champs status, dans notre cas, status_mission
  //Car ce champs n'est pas renseign dans la variable columns mais plutt badges qui est renseign
  //C'est utilis uniquement pour la gestion des status (badges)
  @Input() columnStatusName: string;

  //Pagination
  @Input() pageSizeOptions: number[]; //La taille de la page  afficher
  @Input() defaultPageSize: number; // La taille de la page par dfaut
  @Input() defaultSort: MatSort; //Pour spcifier le tri par dfaut




  ngAfterViewInit() {
    //Initialisation de la pagination
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

  }

  ngOnInit() {
    //Initialisation des donnes
    this.dataSource.data = this.data;
    if (this.defaultSort) {
      this.dataSource.sort = this.defaultSort;
    }
  }

  //Ajout d'un lment en haut  dans la datatable
  addElement(element: any) {
    this.dataSource.data.unshift(element);
    this.dataSource._updateChangeSubscription();
  }

  //Mise  jour d'un element de la datatable
  updateElement(oldElement: any, newElement: any) {
    // On rcupre l'index de l'lment  modifier
    const index = this.dataSource.data.indexOf(oldElement);
    //Si l'index existe
    if (index > -1) {
      //On met  jour l'lement
      for (let i in this.columns) {
        this.dataSource.data[index][this.columns[i]] = newElement[this.columns[i]];

      }
    }
  }
  //Suppression d'un lment de la datatable
  removeElement(element: any) {
    const index = this.dataSource.data.indexOf(element);
    if (index > -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource._updateChangeSubscription();
    }
  }

  /*----Gestion des badges ( ou status) -----*/

  getStatusLabel(status: number, mappings: { code: number, label: string, color: string }[]): string {

    const matchingStatus = mappings.find(mapping => mapping.code === status);

    return matchingStatus ? matchingStatus.label : '';
  }

  getBadgeColor(status: number, mappings: { code: number, label: string, color: string }[]): string {
    const matchingStatus = mappings.find(mapping => mapping.code === status);
    return matchingStatus ? matchingStatus.color : '';
  }

  /*------- End gestion des badges -------*/
}
